body {
  background-color: #f0f0f0;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.container {
  max-width: 800px;
  background-color: #fff;
  border-radius: 8px;
  margin: 20px auto;
  padding: 0 20px;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

h1 {
  text-align: center;
  color: #333;
  margin-top: 30px;
}

.search-form {
  margin-top: 20px;
  display: flex;
}

.search-form input[type="text"] {
  border: 1px solid #ccc;
  border-radius: 4px;
  outline: none;
  flex: 1;
  padding: 10px;
  font-size: 16px;
}

.search-form button {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 4px;
  outline: none;
  padding: 10px 20px;
  font-size: 16px;
}

.gallery {
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  margin-top: 20px;
  display: grid;
}

.photo-card {
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

.photo-card img {
  width: 100%;
  height: auto;
  display: block;
}

.info {
  background-color: #fff;
  padding: 10px;
}

.info-item {
  color: #666;
  margin: 0;
  font-size: 14px;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 4px;
  outline: none;
  margin: 20px auto;
  padding: 10px 20px;
  font-size: 16px;
  display: block;
}

.load-more:hover {
  background-color: #45a049;
}

